@import url('https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Share+Tech+Mono&display=swap');
@import url('https://fonts.googleapis.com/css?family=Rubik&display=swap');
@import url('https://fonts.googleapis.com/css?family=Quicksand&display=swap');
@import url('https://fonts.googleapis.com/css?family=Alatsi&display=swap');
@import url('https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap');

$font-bebas : 'Bebas Neue';
$font-nunito : 'Nunito';
$font-sand : 'Quicksand';
$font-rubik : 'Rubik';
$font-atlatsi : 'Atlatsi';
$font-tech : 'Share Tech Mono'; //usedd

$cyan-l: #10c286;
$cyan : rgba(1, 150, 100, 1);
$cyan-d : rgba(19, 105, 82, 1);
$dark : rgb(34, 36, 36);
$grey : rgba(52, 54, 54, 1);
$grey-blue : rgb(24, 88, 74);
$light : rgba(149, 151, 150, 1);

@mixin gradient {
    background: rgb(25, 35, 37); /* Old browsers */
    background: -moz-linear-gradient(left,  rgba(13,17,18,1) 0%, rgba(29,49,48,1) 45%, $cyan-d 85%, $cyan 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left,  rgba(13,17,18,1) 0%,rgba(29,49,48,1) 45%, $cyan-d 85%, $cyan 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right,  rgba(13,17,18,1) 0%,rgba(29,49,48,1) 45%, $cyan-d 85%, $cyan 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0d1112', endColorstr='#198c71',GradientType=1 ); /* IE6-9 */
}